import React from "react";
import {StaticQuery,Link,graphql} from 'gatsby';
import ArticleEntry from './articleEntry4List';

const Articles = ({ articles, topSubTitle }) => {
  const leftArticlesCount = Math.ceil(articles.length / 5);
  const leftArticles = articles.slice(0, leftArticlesCount);
  const rightArticles = articles.slice(leftArticlesCount, articles.length);

  return (
    <>
      <h1 className="my-4 mt-2">
        <StaticQuery 
          query={graphql`
            query {
              strapiGlobal {
                siteName
              }
            }`
          }
          render={ (data)=>{
            return <>{data.strapiGlobal.siteName}</>
          }}/>
        <small> - { topSubTitle || '全部文章' }</small>
      </h1>

      {
        articles.map( article => <ArticleEntry article={article}/> )
      }
    </>
    

  );

};

export default Articles;