import React from "react";

import {formatIso8061Date} from '../util';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Card from 'react-bootstrap/Card';
import Img from 'gatsby-image';
import {Link} from 'gatsby';

export default (props)=>{
    let article = props.article;   
    return (
        <Card className="mb-4">
            <Link to={`/article/${article.node.slug}`}>
            <Card.Img 
                as={Img}  
                fixed={article.node.image.childImageSharp.fixed}
                imgStyle={{ position: "static" }} />

            <Card.Body>
                [ {article.node.category.name} ]
                <Card.Title>
                {article.node.title}
                </Card.Title>
                <Card.Text>

                </Card.Text>

                
            </Card.Body>
            </Link>
            <Card.Footer>
            <Row style={{fontSize:'small'}}>
                <Col>
                {article.node.author.picture && (
                    <Img
                    fixed={article.node.author.picture.childImageSharp.fixed}
                    imgStyle={{ position: "static", borderRadius: "50%" }}
                    />
                )}
                {article.node.author.name}
                </Col>
                <Col className="text-right">
                <span className="text-right">發表於  {formatIso8061Date(article.node.publishedAt)}</span>
                </Col>
            </Row>
            <Row>
                <Col>
                { article.node.tags && article.node.tags.length>0 && 
                    <div style={{fontSize:'small'}}>
                    標籤:
                    {
                        article.node.tags.map(tag => {
                            return (
                                <Link to={`/tag/${tag.slug}`}  className="px-1">{`#${tag.tagName}`}</Link>
                            )
                        })
                    }
                    </div>
                }
                </Col>
            </Row>
            </Card.Footer>
        </Card>
    );
};